      'frustrated': { backgroundColor: '#b91c1c' },
      'overwhelmed': { backgroundColor: '#991b1b' },
      'pressured': { backgroundColor: '#f87171' },
      'skeptical': { backgroundColor: '#f59e0b' },
      'resigned': { backgroundColor: '#fca5a5' },
      'compromising': { backgroundColor: '#fbbf24' },
      'uncertain': { backgroundColor: '#f59e0b' },
      'burdened': { backgroundColor: '#ef4444' },
      'stretched': { backgroundColor: '#f87171' },
      'cautious': { backgroundColor: '#fbbf24' },
      'distracted': { backgroundColor: '#f97316' },
      'reactive': { backgroundColor: '#ea580c' },
      'vigilant': { backgroundColor: '#f59e0b' },
      'accepting': { backgroundColor: '#fcd34d' },
      'limited': { backgroundColor: '#f97316' },
      'vulnerable': { backgroundColor: '#f87171' },
      
