      'focused': { backgroundColor: '#10b981' },
      'proactive': { backgroundColor: '#059669' },
      'productive': { backgroundColor: '#059669' },
      'value-adding': { backgroundColor: '#047857' },
      'protected': { backgroundColor: '#059669' }
