import React, { useState, useEffect, CSSProperties } from 'react';

const TCOCalculator = () => {
  // Default initial values
  const [inputs, setInputs] = useState({
    numCellLines: 50,
    numYears: 5,
    cellLinesAdded: 10,
    hoursPerWeek: 5,
    hourlyCost: 50,
    costPerCryovial: 20,
    freezerCost: 15000,
    annualMaintenance: 1500,
    ln2Cost: 2400,
    labSpaceCost: 500,
    coreFacilityCost: 150,
    commercialBankCost: 350,
    claasCostPerLine: 200
  });

  const [results, setResults] = useState({
    localStorageCost: 0,
    coreFacilityCost: 0,
    commercialBankCost: 0,
    claasCost: 0
  });

  // Styles with proper TypeScript types
  const styles: Record<string, CSSProperties> = {
    container: {
      fontFamily: 'Arial, sans-serif',
      maxWidth: '1200px',
      margin: '0 auto',
      padding: '20px',
      backgroundColor: 'white',
      borderRadius: '8px',
      boxShadow: '0 4px 6px rgba(0,0,0,0.1)',
    },
    title: {
      fontSize: '24px',
      fontWeight: 'bold',
      marginBottom: '24px',
      textAlign: 'center' as const,
    },
    grid: {
      display: 'grid',
      gridTemplateColumns: 'repeat(auto-fit, minmax(300px, 1fr))',
      gap: '24px',
    },
    panel: {
      backgroundColor: '#f9fafb',
      padding: '16px',
      borderRadius: '8px',
    },
    panelTitle: {
      fontSize: '18px',
      fontWeight: '600',
      marginBottom: '16px',
    },
    inputGrid: {
      display: 'grid',
      gridTemplateColumns: '1fr',
      gap: '16px',
    },
    inputGroup: {
      marginBottom: '12px',
    },
    label: {
      display: 'block',
      fontSize: '14px',
      fontWeight: '500',
      marginBottom: '4px',
    },
    input: {
      width: '100%',
      padding: '8px',
      border: '1px solid #d1d5db',
      borderRadius: '4px',
    },
    resultCard: {
      padding: '16px',
      borderRadius: '8px',
      marginBottom: '24px',
    },
    resultTitle: {
      fontWeight: '500',
      marginBottom: '8px',
    },
    resultValue: {
      fontSize: '24px',
      fontWeight: 'bold',
      marginTop: '8px',
    },
    note: {
      fontSize: '12px',
      color: '#6b7280',
      marginTop: '8px',
    },
    lowest: {
      backgroundColor: '#fef3c7',
      border: '2px solid #f59e0b',
    },
    best: {
      backgroundColor: '#d1fae5',
      border: '2px solid #10b981',
    },
    savings: {
      backgroundColor: '#dbeafe',
      padding: '16px',
      borderRadius: '8px',
      marginTop: '24px',
    },
    savingsGrid: {
      display: 'grid',
      gridTemplateColumns: 'repeat(auto-fit, minmax(150px, 1fr))',
      gap: '8px',
      marginTop: '8px',
    },
    footer: {
      marginTop: '24px',
      fontSize: '14px',
      color: '#6b7280',
    },
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setInputs(prev => ({
      ...prev,
      [name]: parseFloat(value) || 0
    }));
  };

  // Calculate TCO whenever inputs change
  useEffect(() => {
    calculateTCO();
  }, [inputs]);

  const calculateTCO = () => {
    // Local Storage calculation
    const totalCellLinesYear5 = inputs.numCellLines + (inputs.cellLinesAdded * inputs.numYears);
    const equipmentCost = inputs.freezerCost;
    const maintenanceCost = inputs.annualMaintenance * inputs.numYears;
    const ln2Cost = inputs.ln2Cost * inputs.numYears;
    const labSpaceCost = inputs.labSpaceCost * 12 * inputs.numYears;
    const laborCost = (inputs.hoursPerWeek * 52 * inputs.numYears) * inputs.hourlyCost;
    const vialCost = (inputs.numCellLines + (inputs.cellLinesAdded * inputs.numYears * 0.5)) * inputs.costPerCryovial;
    
    const localStorageTotalCost = equipmentCost + maintenanceCost + ln2Cost + labSpaceCost + laborCost + vialCost;
    
    // Core Facility calculation
    const coreFacilityYearlyCost = inputs.coreFacilityCost * 12;
    let coreFacilityTotalLines = 0;
    
    for (let year = 1; year <= inputs.numYears; year++) {
      coreFacilityTotalLines += inputs.numCellLines + (inputs.cellLinesAdded * (year - 1));
    }
    
    const coreFacilityTotalCost = coreFacilityYearlyCost * inputs.numYears + (coreFacilityTotalLines * inputs.costPerCryovial * 0.5);
    
    // Commercial Bank calculation
    let commercialBankTotalLines = 0;
    
    for (let year = 1; year <= inputs.numYears; year++) {
      commercialBankTotalLines += inputs.numCellLines + (inputs.cellLinesAdded * (year - 1));
    }
    
    const commercialBankTotalCost = commercialBankTotalLines * inputs.commercialBankCost;
    
    // CLaaS calculation
    let claasTotalLines = 0;
    
    for (let year = 1; year <= inputs.numYears; year++) {
      claasTotalLines += inputs.numCellLines + (inputs.cellLinesAdded * (year - 1));
    }
    
    const claasTotalCost = claasTotalLines * inputs.claasCostPerLine;

    setResults({
      localStorageCost: Math.round(localStorageTotalCost),
      coreFacilityCost: Math.round(coreFacilityTotalCost),
      commercialBankCost: Math.round(commercialBankTotalCost),
      claasCost: Math.round(claasTotalCost)
    });
  };

  const formatCurrency = (value: number): string => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD',
      minimumFractionDigits: 0,
      maximumFractionDigits: 0
    }).format(value);
  };

  // Find the minimum cost option
  const minCost = Math.min(
    results.localStorageCost,
    results.coreFacilityCost,
    results.commercialBankCost,
    results.claasCost
  );

  return (
    <div style={styles.container}>
      <h2 style={styles.title}>Cell Line Management Total Cost of Ownership Calculator</h2>
      
      <div style={styles.grid}>
        <div style={styles.panel}>
          <h3 style={styles.panelTitle}>Input Parameters</h3>
          
          <div style={styles.inputGrid}>
            <div style={styles.inputGroup}>
              <label style={styles.label}>Initial Cell Lines</label>
              <input
                type="number"
                name="numCellLines"
                value={inputs.numCellLines}
                onChange={handleInputChange}
                style={styles.input}
              />
            </div>
            
            <div style={styles.inputGroup}>
              <label style={styles.label}>New Cell Lines Per Year</label>
              <input
                type="number"
                name="cellLinesAdded"
                value={inputs.cellLinesAdded}
                onChange={handleInputChange}
                style={styles.input}
              />
            </div>
            
            <div style={styles.inputGroup}>
              <label style={styles.label}>Time Period (Years)</label>
              <input
                type="number"
                name="numYears"
                value={inputs.numYears}
                onChange={handleInputChange}
                style={styles.input}
              />
            </div>
            
            <div style={styles.inputGroup}>
              <label style={styles.label}>Hours Per Week for Maintenance</label>
              <input
                type="number"
                name="hoursPerWeek"
                value={inputs.hoursPerWeek}
                onChange={handleInputChange}
                style={styles.input}
              />
            </div>
            
            <div style={styles.inputGroup}>
              <label style={styles.label}>Scientist Hourly Cost ($)</label>
              <input
                type="number"
                name="hourlyCost"
                value={inputs.hourlyCost}
                onChange={handleInputChange}
                style={styles.input}
              />
            </div>
            
            <div style={styles.inputGroup}>
              <label style={styles.label}>Freezer Cost ($)</label>
              <input
                type="number"
                name="freezerCost"
                value={inputs.freezerCost}
                onChange={handleInputChange}
                style={styles.input}
              />
            </div>
            
            <div style={styles.inputGroup}>
              <label style={styles.label}>Annual Maintenance Cost ($)</label>
              <input
                type="number"
                name="annualMaintenance"
                value={inputs.annualMaintenance}
                onChange={handleInputChange}
                style={styles.input}
              />
            </div>
            
            <div style={styles.inputGroup}>
              <label style={styles.label}>Core Facility Monthly Cost ($)</label>
              <input
                type="number"
                name="coreFacilityCost"
                value={inputs.coreFacilityCost}
                onChange={handleInputChange}
                style={styles.input}
              />
            </div>
            
            <div style={styles.inputGroup}>
              <label style={styles.label}>Commercial Bank Cost Per Line ($)</label>
              <input
                type="number"
                name="commercialBankCost"
                value={inputs.commercialBankCost}
                onChange={handleInputChange}
                style={styles.input}
              />
            </div>
            
            <div style={styles.inputGroup}>
              <label style={styles.label}>CLaaS Cost Per Line Per Year ($)</label>
              <input
                type="number"
                name="claasCostPerLine"
                value={inputs.claasCostPerLine}
                onChange={handleInputChange}
                style={styles.input}
              />
            </div>
          </div>
        </div>
        
        <div>
          <h3 style={styles.panelTitle}>5-Year Total Cost of Ownership</h3>
          
          <div style={{ display: 'flex', flexDirection: 'column', gap: '24px' }}>
            <div style={{
              ...styles.resultCard, 
              ...(results.localStorageCost === minCost ? styles.lowest : {})
            } as CSSProperties}>
              <h4 style={styles.resultTitle}>Local Storage</h4>
              <div style={styles.resultValue}>{formatCurrency(results.localStorageCost)}</div>
              {results.localStorageCost === minCost && (
                <div style={styles.note}>(Lowest upfront cost but highest long-term risk)</div>
              )}
            </div>
            
            <div style={{
              ...styles.resultCard, 
              ...(results.coreFacilityCost === minCost ? styles.lowest : {})
            } as CSSProperties}>
              <h4 style={styles.resultTitle}>Core Facility Storage</h4>
              <div style={styles.resultValue}>{formatCurrency(results.coreFacilityCost)}</div>
              {results.coreFacilityCost === minCost && (
                <div style={styles.note}>(Lowest cost but limited services)</div>
              )}
            </div>
            
            <div style={{
              ...styles.resultCard, 
              ...(results.commercialBankCost === minCost ? styles.lowest : {})
            } as CSSProperties}>
              <h4 style={styles.resultTitle}>Commercial Cell Banks</h4>
              <div style={styles.resultValue}>{formatCurrency(results.commercialBankCost)}</div>
              {results.commercialBankCost === minCost && (
                <div style={styles.note}>(Lowest cost option but limited flexibility)</div>
              )}
            </div>
            
            <div style={{
              ...styles.resultCard, 
              ...(results.claasCost === minCost ? styles.best : {})
            } as CSSProperties}>
              <h4 style={styles.resultTitle}>SyntaxisBio CLaaS</h4>
              <div style={styles.resultValue}>{formatCurrency(results.claasCost)}</div>
              {results.claasCost === minCost ? (
                <div style={{ color: '#047857', marginTop: '8px', fontSize: '14px' }}>
                  (Best value with lowest total cost and highest security)
                </div>
              ) : (
                <div style={{ color: '#1d4ed8', marginTop: '8px', fontSize: '14px' }}>
                  Premium service with highest security and provenance tracking
                </div>
              )}
            </div>
            
            <div style={styles.savings}>
              <h4 style={styles.resultTitle}>Potential Savings with CLaaS</h4>
              <div style={styles.savingsGrid}>
                <div>
                  <div style={{ fontSize: '14px' }}>vs. Local Storage:</div>
                  <div style={{ fontWeight: 'bold', color: '#1d4ed8' }}>
                    {formatCurrency(results.localStorageCost - results.claasCost)}
                  </div>
                </div>
                <div>
                  <div style={{ fontSize: '14px' }}>vs. Core Facility:</div>
                  <div style={{ fontWeight: 'bold', color: '#1d4ed8' }}>
                    {formatCurrency(results.coreFacilityCost - results.claasCost)}
                  </div>
                </div>
                <div>
                  <div style={{ fontSize: '14px' }}>vs. Commercial Banks:</div>
                  <div style={{ fontWeight: 'bold', color: '#1d4ed8' }}>
                    {formatCurrency(results.commercialBankCost - results.claasCost)}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      
      <div style={styles.footer}>
        <p>This calculator provides an estimate of the total cost of ownership over {inputs.numYears} years for different cell line management solutions.</p>
        <p style={{ marginTop: '4px' }}>Actual costs may vary based on specific institutional requirements and pricing changes.</p>
      </div>
    </div>
  );
};

export default TCOCalculator;
